<template>
  <div id="historyDetail" :style="{
  'background': `url('static/smogBg/history.jpg') no-repeat 0 0 / cover fixed`}">
    <div class="inner">
      <h2>{{this.$route.query.city}}历史天气 {{this.$route.query.time.replace('-','年')}}月份</h2>
      <ul class="clearfix">
        <li class="title clearfix">
          <span>日期</span>
          <span>天气状况</span>
          <span>气温</span>
          <span>风力风向</span>
        </li>
        <li class="con clearfix" v-for="item in detailList">
          <span>{{item.time}}</span>
          <span>{{item.status}}</span>
          <span>{{item.wendu}}</span>
          <span>{{item.wind}}</span>
        </li>
      </ul>
      <loading v-if="!detailList.length && !error"></loading>
      <wrong v-if="error"></wrong>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import { getHistoryList } from 'api/getHistoryList'
  import { ERROR } from 'api/config'
  import { mapGetters } from 'vuex'

  import Loading from 'base/loading/loading'
  import Wrong from 'base/error/error'

  export default {
    name: 'history-detail',
    data () {
      return {
        detailList: [],
        error: false
      }
    },
    computed: {
      ...mapGetters([
        'weatherHistory'
      ])
    },
    methods: {
      _getHistoryList () {
        getHistoryList(this.weatherHistory, this.$route.query.time).then((res) => {
          if (res.data.error === ERROR) {
            this.detailList = res.data.data
            this.error = false
          } else {
            this.error = true
          }
        }).catch((res) => {
          this.error = true
        })
      }
    },
    mounted () {
      this._getHistoryList()
    },
    components: {
      Loading,
      Wrong
    }
  }

</script>

<style lang="less" scoped rel="stylesheet/less" type="text/less">
  #historyDetail {
    color: #fff;
    min-height: 100%;
    padding-top: 100px;
    padding-bottom: 20px;
    box-sizing: border-box;
    transition: all 2s;
    font-size: 13px;
    h2 {
      font-size: 28px;
      text-align: center;
      padding: 10px 0;
      box-sizing: border-box;
    }
    ul {
      width: 100%;
      background-color: rgba(0, 0, 0, .5);
      li {
        width: 100%;
        padding: 5px 0;
        box-sizing: border-box;
        border-bottom: 1px solid #eee;
        span {
          float: left;
          display: inline-block;
          width: calc(100% / 4);
          padding: 0 10px;
          box-sizing: border-box;
        }
      }
      .title {
        width: 100%;
        span {
          font-size: 14px;
          font-weight: bold;
          letter-spacing: 2px;
        }
      }
      .con {
        width: 100%;
        span {
          padding: 10px;
          box-sizing: border-box;
        }
      }
    }
  }
</style>


